#root {
  background: #007bff;
  padding: 20px;
  background: linear-gradient(to right, #0062e6, #33aeff);
}

.App {
  background-size: cover;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 30px;
}

.card {
  width: 375px;
  height: 700px;
  display: flex;
  flex-direction: column;
  border: none;
  border-radius: 30px;
  transition: all 0.2s;
  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);
}

.head {
  margin: 1.5rem 0;
}

.title {
  margin: 0.5rem 0;
  font-size: 0.9rem;
  letter-spacing: 0.1rem;
  font-weight: bold;
  text-align: center;
  opacity: 0.7;
}

.price {
  font-size: 3rem;
  margin: 0;
  text-align: center;
}

.plan-details {
  display: flex;
  flex-direction: column;
  font-family: sans-serif;
  font-size: 20px;
  text-align: left;
  gap: 20px;
}
ul li {
  margin-bottom: 1rem;
}
span {
  font-size: 20px;
}
.btn {
  border-radius: 30px;
  width: 300px;
  font-size: 80%;
  letter-spacing: 0.1rem;
  font-weight: bold;
  padding-top: 15px;
  opacity: 0.7;
  transition: all 0.2s;
}

@media (min-width: 600px) {
  .App {
    display: flex;
    flex-direction: row;
  }
  .card:hover {
    margin-top: -0.25rem;
    margin-bottom: 0.25rem;
    box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.3);
  }
  button:hover {
    opacity: 1;
  }
}
